<template>
  <div class="product-list">
    <!-- 下拉列表 -->
    <div class="drop-list">
      <p class="selected" @click="showNameList">
        <a href="javascript:">{{selected.title}}</a>
      </p>
      <transition name="slide-down">
        <scroll class="select-wrapper" :pullup="true" :data="nameList" v-show="showList" @scrollToEnd="getMoreName" ref="scroll">
          <div class="scroll-content">
            <ul class="select-list">
              <li :class="{active: selected.id === 0}">
                <a href="javascript:" @click="changeProduct({id: 0, title: '全部产品'})">全部产品</a>
              </li>
              <li v-for="item in nameList" :key="item.id" :class="{active: selected.id === item.id}">
                <a href="javascript:" @click="changeProduct(item)">{{item.title}}</a>
              </li>
            </ul>
            <load-more :over="nameOpt.over" :is-loading="nameOpt.isLoading" />
          </div>
        </scroll>
      </transition>
    </div>
    <!-- 产品列表 -->
    <scroll class="product-list-wrapper" :data="productList" :pullup="true" @scrollToEnd="getMoreList">
      <div class="scroll-content">
        <ul class="product-list">
          <transition-group name="fade-slide-up">
            <li class="item" v-for="product in productList" :key="product.id" @click="toDetail(product)">
              <div class="top">
                <div class="left">产品名称：{{product.title}}</div>
                <div class="right">{{product.time}}</div>
              </div>
              <div class="bottom">
                <span>投资金额：</span>
                <span class="currency">{{product.money}}</span>
              </div>
            </li>
          </transition-group>
        </ul>
        <load-more :over="productOpt.over" :is-loading="productOpt.isLoading" />
      </div>
    </scroll>
    <transition name="fade">
      <div class="mask" v-show="showList" @click="showList=false"></div>
    </transition>
  </div>
</template>

<script>
import { getProductName, getProductList } from '@/api/product'
import { AID, ERR_OK } from '@/api/config'

import Scroll from '@/base/scroll/scroll'
import LoadMore from '@/base/load-more/load-more'

export default {
  data() {
    return {
      // 产品列表数组
      productList: [],
      // 产品名称数组
      nameList: [],

      // 显示产品名称列表
      showList: false,

      // 选中的产品
      selected: { id: 0, title: '全部产品' },

      // 产品名称加载状态
      nameOpt: {
        limit: 10,
        offset: 0,
        over: false,
        isLoading: false
      },
      // 产品列表加载状态
      productOpt: {
        limit: 10,
        offset: 0,
        over: false,
        isLoading: false
      }
    }
  },
  created() {
    this._getName()
    this._getList()
  },
  methods: {
    // 获取产品名称
    _getName() {
      this.nameOpt.isLoading = true
      const limit = this.nameOpt.limit;
      const offset = this.nameOpt.offset;

      getProductName({ limit, offset })
        .then(res => {
          /**
           * 请求成功，但后端返回状态码是 0
           */
          // if (res.data.code === ERR_OK) {
          this.nameList.push(...res.data.data.list)

          this.nameOpt.isLoading = false

          if (res.data.data.list.length < this.nameOpt.limit) {
            this.nameOpt.over = true
          }
          // } else {
          //   this.$dialog.alert({
          //     mes: res.data.msg,
          //     callback() {}
          //   })
          // }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 获取产品列表
    _getList(id) {
      this.productOpt.isLoading = true
      const limit = this.productOpt.limit;
      const offset = this.productOpt.offset;

      getProductList({ id, limit, offset })
        .then(res => {
          if (res.data.code === ERR_OK) {
            this.productList.push(...res.data.data.list)

            this.productOpt.isLoading = false

            if (res.data.data.list.length < this.productOpt.limit) {
              this.productOpt.over = true
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 获取更多产品
    getMoreList() {
      if (this.productOpt.over) {
        return
      }

      this.productOpt.offset += 10

      this._getList()
    },
    // 获取更多产品名称
    getMoreName() {
      if (this.nameOpt.over) {
        return
      }

      this.nameOpt.offset += 10

      this._getName()
    },
    // 显示产品名称列表
    showNameList() {
      this.showList = true

      // 重新计算scroll高度
      setTimeout(
        function() {
          this.$refs.scroll.refresh()
        }.bind(this),
        20
      )
    },
    // 下拉列表过滤产品
    changeProduct(item) {
      if (item.id === this.selected.id) {
        return
      }

      this.selected = item
      this.productList = []

      this.showList = false
      this.productOpt = {
        limit: 10,
        offset: 0,
        over: false,
        isLoading: false
      }

      this._getList(item.id)
    },
    // 点击产品列表
    toDetail(product) {
      this.$router.push(`/product/${product.id}/detail/aid/${AID}`);
    }
  },
  components: {
    Scroll,
    LoadMore
  }
}
</script>

<style scoped lang="stylus">
@import "~@/common/stylus/variable"
@import "~@/common/stylus/mixin"

// 动画
animation-slide-down(0.2s)
animation-fade(0.5s)
.fade-slide-up-enter-active
  opacity 1
  transform translateY(0)
  transition all .5s
.fade-slide-up-enter, .fade-slide-up-leave-to
  opacity 0
  transform translateY(1rem)
// main
.product-list
  background-color #efefef
  font-size 0
  // 产品下拉列表
  .drop-list
    position relative
    width 100%
    height 0.8rem
    background-color #fff
    z-index 100
    text-align center
    line-height 0.8rem
    .selected
      position relative
      background-color #fff
      box-shadow 0 0.01rem 0.05rem 0.03rem #eee
      z-index 20
      a
        display block
        height 0.8rem
        font-size 0.3rem
        overflow hidden
        text-overflow ellipsis
        white-space nowrap
        &::before
          content " "
          position absolute
          top 0.28rem
          right 0.25rem
          border-left 0.22rem solid transparent
          border-right 0.22rem solid transparent
          border-top 0.24rem solid #aaa
        &::after
          content " "
          position absolute
          top 0.23rem
          right 0.25rem
          border-left 0.22rem solid transparent
          border-right 0.22rem solid transparent
          border-top 0.24rem solid #fff
    .select-wrapper
      position relative
      max-height 8rem
      overflow hidden
      background-color #fff
      .select-list
        background-color #fff
        li
          a
            display block
            min-height 0.8rem
            padding 0.01rem 0
            border-bottom 0.01rem solid #eee
            font-size 0.3rem
            word-break break-all
          &.active
            background-color $color-main
            a
              color #fff
      .loading
        line-height normal
  // 产品列表
  .product-list-wrapper
    position absolute
    width 100%
    height calc(100% - 0.8rem)
    padding-top 0.15rem
    background-color #efefef
    overflow hidden
    .scroll-content
      position absolute
      width 100%
      .product-list
        position relative
        padding-bottom 0.15rem
        z-index 10
        .item
          display flex
          flex-direction column
          padding 0.3rem
          margin-bottom 0.17rem
          box-shadow 0 3px 5px 1px #ddd
          background-color #fff
          font-size 0.34rem
          &:last-child
            margin-bottom 0
          .top
            display flex
            justify-content space-between
            margin-bottom 0.34rem
            .left
              flex-grow 1
              overflow hidden
              text-overflow ellipsis
              word-break break-all
              white-space nowrap
            .right
              flex-shrink 0
              color #999
          .bottom
            .currency
              color $color-main
.mask
  position fixed
  width 100%
  height 100%
  background-color $color-toast
  touch-action none
</style>



